import ObjectJS from "./ThreeObject"
import * as THREE from 'three';	
import * as Plane from "./ThreePlane"
export class CubeJS extends ObjectJS{
    constructor(x,y,z,size,isBuff=false){
        super(x,y,z,size);
        this.isBuff=isBuff;
    }
    initObjectFromScene(){
        let obj=this.isBuff?createCubBuffer(this.x,this.y,this.z,this.size,this.color):createCub(this.x,this.y,this.z,this.size,this.color);
        this.setObject(obj);
        return obj;
    }
}

export function createCub(x,y,z,size,color){
    const materialclick = color;
    const geometry = new THREE.BoxGeometry(size,size,size);
    let cube=new THREE.Mesh( geometry, materialclick );
	//Use position instead of translate(change repere)
	cube.position.set(x,y,z);
    return cube;
}

export function createCubBuffer(x,y,z,size,color){
    let group=new THREE.Group();
    let planeX=Plane.createMesh(Plane.getVerticleX(size),Plane.getUvX(),color)
    planeX.name="X Plane(Upper)";
    let planeMinX=Plane.createMesh(Plane.getMinusVerticleX(size),Plane.getMinusUvX(),color)
    planeMinX.name="-X Plane(Lower)"
    let planeZ=Plane.createMesh(Plane.getVerticleZ(size),Plane.getUvZ(),color)
    planeZ.name="Z Plane(Front)"
    let planeMinZ=Plane.createMesh(Plane.getMinusVerticleZ(size),Plane.getMinusUvZ(),color)
    planeMinZ.name="-Z Plane(Back)"
    let planeY=Plane.createMesh(Plane.getVerticleY(size),Plane.getUvX(),color)
    planeY.name="Y Plane(Right)"
    let planeMinY=Plane.createMesh(Plane.getMinusVerticleY(size),Plane.getMinusUvX(),color)
    planeMinY.name="-Y Plane(Left)"
    //We need move only minus
    //We can only show in upper camera
    planeX.position.set(0,0,0);
    //We can only show in lower camera
    planeMinX.position.set(0,-1,0);
    planeZ.position.set(0,0,0);
    planeMinZ.position.set(0,0,-1);
    planeY.position.set(0,0,0);
    planeMinY.position.set(-1,0,0);
    group.add(planeX);
    group.add(planeMinX);
    group.add(planeZ);
    group.add(planeMinZ);
    group.add(planeY);
    group.add(planeMinY);
    group.position.set(x,y,z);
    return group;
}