//Voir :https://www.toptal.com/developers/keycode/for/alt
export default class KeyControlCamera{   
    constructor(){
        this._listCalled=[];
    }
    addOnKeyPressed(keyCode,funct){
        this._listCalled[keyCode]=funct;       
        let listCalled=this._listCalled;
        window.onkeyup=function(evt){
            if (listCalled[evt.code]!=null){
                listCalled[evt.code](true);
            }
//            console.log(evt.code)
//            console.warn(listCalled)
        }
        window.onkeydown=function(evt){
            if (evt.repeat){
                return;
            }
            if (listCalled[evt.code]!=null){
                listCalled[evt.code](false);
            }
        }
    }
}