/**
 * Class DataControl allow Camera to control key
 */
export default class MouseControlCamera{
    constructor(){
        this.oldMouseX=0;
        this.oldMouseY=0;
        this.mouseX=0;
        this.mouseY=0;
    }
    setOnMouseUpOrDown(funct){
        let dataControl=this;
        let functionThis=funct.bind(this);
        window.onmousedown=function(evt){
//            console.log("down");     
            dataControl.oldMouseX=dataControl.mouseX;
            dataControl.oldMouseY=dataControl.mouseY;
            functionThis(false);
        }
        window.onmouseup=function(evt){
//            console.log("up");
            functionThis(true);
        }
    }
    setOnMouseMoved(funct){
        let dataControl=this;
        let functionThis=funct.bind(this);
        window.onmousemove=function(evt){
            dataControl.mouseX=evt.screenX;
            dataControl.mouseY=evt.screenY;
            functionThis();
            dataControl.oldMouseX=dataControl.mouseX;
            dataControl.oldMouseY=dataControl.mouseY;
        }
    }
}