import {CubeJS} from "./data/ThreeCube"
import PlaneJS from "./data/ThreePlane"
import Screen from "./screen"
import { Component } from "react"
import Data from "../privateDoc/cloud_background.jpg";
import Cat from "../privateDoc/chat_moche.png";
import Wolf from "../privateDoc/wolf_real.jpg"
import PlaneGreen from "../privateDoc/noice_green.png"
import MouseControlCamera from "./data/MouseControlCamera"
import KeyControlCamera from "./data/KeyControlCamera"
export default class MainScreen extends Component{
    constructor(props){
        super(props)
        var cube=new CubeJS(0,0,-1,0.5);
        cube.nameObject("Cube green moving");
        var cube2=new CubeJS(0,0,1,0.5);
        cube2.nameObject("Red saing cube");
        var cube3=new CubeJS(-1,0,0,0.5,true);
        cube3.nameObject("Wolf cube");
        var plane=new PlaneJS(0.0,-3,-2);
        plane.nameObject("The plane ")
        var ground=new PlaneJS(0.0,-3.1,-2,100);
        plane.nameObject("The floor")
        var onflying=true;
        function reset(){
            console.warn("NOPE");;
        }
        this.state={reset:reset,cube:cube,cube2:cube2,cube3:cube3,plane:plane,ground:ground,onflying:onflying}
    }
    init(){
        console.log("NO")
    }
    
    /**
     * Move in Z parameter depending of Camera
     * @param {SceneJS} scene : A Scene to move camera
     */
    //Needed to show 3D screen 
    render(){
        //Make children background to be read !
        var cube=this.state.cube;
        var cube2=this.state.cube2;
        var cube3=this.state.cube3;
        var plane=this.state.plane;
        var ground=this.state.ground;
        console.log(Data);
        var mainScreen=this;
        //Allow to move left or right (-1 or 1)
        var xMove=0;
        var yMove=0;
        //allow to move forward or backward (-1 or 1 )        
        var zMove=0;
        var mouseData=new MouseControlCamera();
        var keyData=new KeyControlCamera();
        function onMovedX(number){
            return function(isUp){
                xMove=isUp?0:number;
            }
        }
        function onMovedZ(number){
            return function(isUp){
                zMove=isUp?0:number;
            }
        }
        function onInitScene(){
            var sceneT=this;
            mainScreen.setState({reset:function(){
                sceneT.setPositionCamera(0,0,0);
                sceneT.setRotationCamera(0,0,0);    
            }})
            this.setBackgroundTexture(Data);
            cube.setColorOfObject(0x00ff00);
            cube2.setColorOfObject(0xff0000);
            cube3.setTextureOfObject(Wolf);
            plane.setTextureOfObject(Cat);
            ground.setTextureOfObject(PlaneGreen);
            this.addObject(cube);
            this.addObject(cube2);
            this.addObject(plane);
            this.addObject(ground);
            this.addObject(cube3);
            var scene=this;
            mainScreen.init=function(){
                mainScreen.setState({scene:scene})
            }
            var isMovingCamera=false;
            mouseData.setOnMouseUpOrDown(function(up){
                isMovingCamera=!up;       
            })
            mouseData.setOnMouseMoved(function(){
                console.log(isMovingCamera);
                if (isMovingCamera){
                    // Turn up or down
                    let resultY=(this.oldMouseY-this.mouseY);
                    // Turn left or right 
                    let resultX=(this.oldMouseX-this.mouseX);
                    console.log(this.oldMouseY+"-"+this.mouseY+"="+this.resultY);
                    //Limit the turn by 180 (90=1.5);                    
                    console.log(scene.rx);
                    const MAXRADIAN=90*Math.PI/180
                    if ((scene.rx>MAXRADIAN && resultY>0) || (scene.rx<-MAXRADIAN && resultY<0)){
                        resultY=0;
                    }
                    scene.rotateTo(resultY,resultX,undefined,undefined);
                } 
            })
            keyData.addOnKeyPressed("ArrowUp",onMovedZ(-1));
            keyData.addOnKeyPressed("ArrowDown",onMovedZ(1));
            keyData.addOnKeyPressed("ArrowLeft",onMovedX(-1));
            keyData.addOnKeyPressed("ArrowRight",onMovedX(1));
        }
        function onSceneUpdate(){
            cube.setRotation(cube.rotateX+this.getDelta());
            //Use rotation's matrix to move  by flying
            let rotationFlying=this.getRotation();
            let main=mainScreen;
            //If it's not flying...
            if (!main.state.onflying){
                //Remove possibilities to fly ...
                rotationFlying.clone();
            }
            this.moveTo(xMove,yMove,zMove,0.1,rotationFlying);
        }
        return <Screen beforeSceneInit={onInitScene} onSceneUpdate={onSceneUpdate}>
            <button onClick={mainScreen.state.reset}>Reset</button><br/>
                MyScreen
            </Screen>
    }
    componentDidMount(){
        console.log("INITIATED");
        this.init();
    }
}   