import * as THREE from 'three';	
import { WEBGL } from 'three/examples/jsm/WebGL.js';
import { Matrix3 } from 'three';
import ObjectJS from './ThreeObject';



export default class SceneJS{
	constructor(wW,wH,x=0,y=0,z=0){
		this.width=wW;
		this.heigth=wH;
		this.clock = new THREE.Clock();
		//Scene to show
		this.scene = new THREE.Scene();
		this.camera = new THREE.PerspectiveCamera( 75, wW / wH, 0.1, 1000 );
		this.renderer = new THREE.WebGLRenderer({precision:"lowp",powerPreference:"low-power" });
		this.setPositionCamera(x,y,z);
		this.setRotationCamera(0,0,0);
		this.renderer.setSize( wW, wH );
		this.renderer.context.getExtension('OES_standard_derivatives');	
		//List of obje 	ct in ObjectJS
		this.onUpdate=function(){

		}
		this.listObject=[];
	}
	getRotation(){
		return this.camera.quaternion;
	}
	/**
	 * Rotate camera from 
	 * @param {*} x :represent the rool : turning head  in up or down ?
	 * @param {*} y : represent the pitch : turning head from left or right ?
	 * @param {*} z :represent the yaw : in^clining head in left or right ?
	 */
	setRotationCamera(x=this.rx,y=this.ry,z=this.rz){
		this.rx=x;		
		this.ry=y;
		this.rz=z;
		//Order are important ,Y before and X after ()
		this.camera.quaternion.setFromEuler(new THREE.Euler(x,y,z,'YXZ'));
		this.camera.updateProjectionMatrix();
	}
	setPositionCamera(x=this.vx,y=this.vy,z=this.vz){
		this.vx=x;
		this.vy=y;
		this.vz=z;
		this.camera.position.set(x, y, z);
		this.camera.updateProjectionMatrix();		
	}
	/**
	 * Allow to move in a certain distance depending of rotation and Delta
	 * @param {Double} x :Left or right ?
	 * @param {Double} y :Up or down ?
	 * @param {Double} z :Forward or backward
	 * @param {Double} delta : Delta to calculate this moving ,default is the delta of three
	 * @param {Quaternion} quaternionToApply : Can be null .. if not null , apply this quaternon here
	 */
	moveTo(x=0.0,y=0.0,z=0.0,delta=this.getDelta(),quaternionToApply){
		let deltaMove=new THREE.Vector3(x, y, z); 
		let trueResult=quaternionToApply!=undefined?deltaMove.applyQuaternion(quaternionToApply):deltaMove;
//		console.log(trueResult)
//		console.log(deltaMove)
		this.setPositionCamera(this.vx+trueResult.x*delta, this.vy+trueResult.y*delta, this.vz+trueResult.z*delta);
	}
	//To radiant
	rotateTo(x=0.0,y=0.0,z=0.0,delta=Math.PI/180,quaternionToApply){
		let deltaMove=new THREE.Vector3(x, y, z); 
		//iF quaternion is here,apply here , apply this deltamove
		let trueResult=quaternionToApply!=undefined?deltaMove.applyQuaternion(quaternionToApply):deltaMove;
		this.setRotationCamera(this.rx+trueResult.x*delta, this.ry+trueResult.y*delta, this.rz+trueResult.z*delta);
	}
	getDelta(){
		return this.clock.getDelta();
	}
	setBackgroundColor(background){
		this.scene.background=new THREE.Color(background);
	}
	setBackgroundTexture(pathTexture){
		this.scene.background=new THREE.TextureLoader().load(pathTexture);
	}
	//Allow to add object in scene
	addObject(threeObject){
		//If is instanceof ObjectJS
		if (threeObject instanceof ObjectJS){
			this.listObject.push(threeObject);
			this.scene.add(threeObject.initObjectFromScene());
		}else{
			this.scene.add(threeObject);
		}
	}
	initScene(documentBody=document.body){
		documentBody.appendChild( this.renderer.domElement );
		let that=this;
		window.scene=this.scene;
		//Update resize when it's the same
		window.addEventListener( 'resize', function() {
			that.camera.aspect = window.innerWidth / window.innerHeight;
			that.camera.updateProjectionMatrix();
			that.renderer.setSize( window.innerWidth, window.innerHeight);
		})
//This is the light from the tree
//		const light = new THREE.AmbientLight(0xffffff ,1);
//		this.scene.add( light );
		var funToRequest=null;
		let animate=function(){
			requestAnimationFrame(funToRequest);			
			that.onUpdate();
			that.renderer.render( that.scene, that.camera );
		}
		funToRequest=animate;
		if ( WEBGL.isWebGLAvailable() ) {
		// Initiate function or other initializations here
			animate();
		} else {
			const warning = WEBGL.getWebGLErrorMessage();
			document.getElementById( 'container' ).appendChild( warning );
			//console.error("WARNING:"+warning)
		}
	}
}