import * as THREE from 'three';	

export default class ObjectJS{
    constructor(x,y,z,size){
        this.x=x;
        this.y=y;
        this.z=z;
        this.size=size;
        this.rotateX=0;
        this.rotateY=0;
        this.rotateZ=0;
        this.object=null;
        this.color=new THREE.MeshBasicMaterial( { color: 0x000000 });        
    }
    //Rename object if possible
    nameObject(name=this.name){
        if (this.object!=null){
            this.object.name=name;
        }
        this.name=name;
    }
    setObject(obj){
        this.object=obj;
        this.nameObject();
    }

    initObjectFromScene(){
        return undefined;
    }
    setColorOfObject(color){
        this.color.dispose();
        this.color=new THREE.MeshBasicMaterial( { color: color });
    }
    
    setTextureOfObject(url){
        this.color.dispose();
        const texture = new THREE.TextureLoader().load(url);
        this.color=new THREE.MeshBasicMaterial( { map: texture } );
    }
    setPosition(x=this.x,y=this.y,z=this.z){
        this.x=x;
        this.y=y;
        this.z=z;
        if (this.object!=null){
            this.object.position.set(x,y,z);
        }
    }
    setRotation(x=this.rotateX,y=this.rotateY,z=this.rotateZ){
        this.rotateX=x;
        this.rotateY=y;
        this.rotateZ=z;
        if (this.object!=null){
            this.object.rotation.set(x,y,z);
        }
    }
}