import { Component } from "react"
import SceneJS from "./data/ThreeScene"
import {CubeJS} from "./data/ThreeCube"
import "../screen.css"
export default class Screen extends Component{
    constructor(props){
        super(props)
        console.log(props)
        let characters=[];
        let w=props.width!=undefined?props.width:window.innerWidth
        let h=props.width!=undefined?props.height:window.innerHeight
        this.state={characters:characters,width:w,heigth:h}
    }
    //Needed to show 3D screen 
    render(){
        //Make children background to be read !
        return <div>
            <div id="background">
             {this.props.children}
            </div>
            <div
                style={{width:this.state.w, height: this.state.h }}
                ref={mount => { this.mount = mount}}
            />
        </div>
    }
    componentDidMount(){
        let scene=new SceneJS(this.state.width,this.state.heigth);
        //Bind this to make initiated
        let beforeSceneFunction=this.props.beforeSceneInit;
        let onUpdateSceneFunction=this.props.onSceneUpdate;
        let beforeSceneInit=beforeSceneFunction?beforeSceneFunction.bind(scene):function(){};
        beforeSceneInit();
        scene.onUpdate=onUpdateSceneFunction?onUpdateSceneFunction.bind(scene):function(){};
        scene.initScene(this.mount);
    }
}